<template>
  <div class="yff-container">
    <h1 class="yff-heading">Yrkesfaglig Fordybning</h1>

    <!-- VELG KLASSE - SECTION -->
    <p class="labels">Velg klasse:</p>
    <select class="select-student" v-model="valgtKlasse">
      <option disabled value="">Velg Klasse</option>
      <option  class="select-option" v-for="(klasse, index) in klasser" :value="klasse" :key="index">{{ klasse }}</option>
    </select>

    <!-- VELG ELEV - SECTION -->
    <p class="labels">Velg elev:</p>
    <select class="select-student" v-model="valgtElev" @change="selectedStudent">
      <option disabled value="">Velg elev</option>
      <!-- her må også etternavn og fornavn snus om på!-->
      <option  class="select-option" v-for="(elev, index) in elever" :value="elev" :key="index">{{ elev.Etternavn }} {{ elev.Fornavn }}</option>
    </select>

    <!-- NÅR ELEV ER VALGT, VISES KATEGORIENE AV LPMs -->
    <app-lpm-valgliste v-if="elevValgtBool"></app-lpm-valgliste>
    <button v-if="elevValgtBool" class="link-btn" @click="toApproval">Neste</button>
  </div>
</template>

<script>
import LpmValgliste from '../components/LpmValgliste.vue'
import _ from 'lodash'
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      elevValgtBool: false
    }
  },
  methods: {

    selectedStudent () {
      if(this.valgtElev !== '' || this.valgtElev !== null){
        this.elevValgtBool = true
      }
      else{
        console.log("error: valgtElev = null")
      }
    },

    toApproval () {
      let utdanningsprog
      let skole = 'Tertnes videregående skole'
      let yearlyHours
      let today = new Date().toISOString().split('T')[0]

      if(this.$store.getters.get_valgtElev.Klasse.substring(0,1) === '1'){
        utdanningsprog = 'Service og samferdsel'
        yearlyHours = '168'
      } else {
        utdanningsprog = 'Salg, service og sikkerhet'
        yearlyHours = '253'
      }
      let propsData = {
        utdanningsprogram: utdanningsprog,
        skole: skole,
        dato: today,
        prosjekter: [
          { 
            prosjektNr:1, 
            laerefag:'Læreplan i salgsfaget Vg3', 
            organisering:'I bedrift og på skole', 
            yearlyHours: yearlyHours
          }
        ]
      }
      this.$store.commit('leggTilProps', propsData)
      this.$router.push('YFF_Godkjenn')
    }
  },
  computed: {
	  elever() {
      let vm = this
      if(vm.$store.getters.get_valgtKlasse === '') return vm.$store.getters.get_klasse
      else
      return _.filter(this.$store.getters.get_klasse, function (elev) {
        return elev.Klasse === vm.$store.getters.get_valgtKlasse
      })
    },
    klasser () {
      const klassenavn = [...new Set(this.$store.getters.get_klasse.map(elev => elev.Klasse))];
      return klassenavn
    },
    valgtElev: {
      get() { return this.$store.state.valgtElev },
      set(elev) { this.$store.commit('velgElev', elev)}
    },
    valgtKlasse: {
      get() { return this.$store.state.valgtKlasse },
      set(klasse) { this.$store.commit('velgKlasse', klasse)}
    }, 
    valgteLPM: {
      get() { return this.$store.state.valgteLPM },
      set(lpm) { this.$store.commit('addLpm', lpm)}
    }
  },
  components: {
    appLpmValgliste:LpmValgliste
  },
  beforeCreate() {
    if (this.$store.getters.get_klasse.length < 1)
    this.$router.push('/')
  }
}
</script>

<style scoped>
  .yff-container{
    width:93%;
    font-family:'Ubuntu', sans-serif;
    text-align: center;
    margin-left:auto;
    margin-right:auto;
    margin-top:70px;
    margin-bottom:20px;
    padding:20px;
    padding-bottom:60px;
    background-color: #078893;
    color:white;
    border-radius: 5px;
    -webkit-box-shadow: 0px 0px 6px -1px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 6px -1px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 6px -1px rgba(0,0,0,0.75);
  }

  .yff-heading{
    font-size: 2rem;
    font-family:'Ubuntu', sans-serif;
    margin-bottom:40px;
  }

  .select-student{
    -webkit-padding-end: 20px;
    -moz-padding-end: 20px;
    -webkit-padding-start: 2px;
    -moz-padding-start: 2px;
    border-radius: 4px;
    font-size: inherit;
    padding-top: 3px;
    padding-bottom: 3px;
    -webkit-padding-end: 20px;
    -moz-padding-end: 20px;
  }

  .link-btn {
    width: 100px;
    height: 40px;
    margin: 10px;
    margin-top: 40px;
    border-radius: 5px;
    border: 1px solid darkslategrey;
    font-weight: bold;
    color: white;
    background-color: #E37222;
  }

  .link-btn:hover {
    background-color: #e88640;
    color: black;
  }

  .link-btn:active {
    background-color: #a85f2b;
    color: white;
  }
  
  .labels{
    margin-bottom:4px;
  }
</style>